/* eslint-disable array-callback-return */
import React, { Component } from "react";
import {addFavorite,deleFavorite} from '../../api/favorite.js'


export default class Home extends Component {
    state = {
        articles: [],//数据
        totalPage:[],//总页数
        rows:10,//每页显示的条数
        currentPage:0,//当前页
        favoritedisabled:false,//数据在加载过程中点赞禁止

    };
    componentDidMount() {
        window.api({
                method: "GET",
                url: "/api/articles",
                params:{
                    limit:this.state.rows
                }
            }).then((item) => {
                // console.log(item,'1111111111111111');
                this.setState({
                    articles: item.data.articles,
                });
                this.setState({
                    totalPage:item.data.articlesCount % this.state.rows === 0 ? item.data.articlesCount / this.state.rows : item.data.articlesCount / this.state.rows + 1
                })
            });
    }
    click = (index)=>{
        this.setState({
            currentPage: index
        }, function (){
            window.api({
                method: "GET",
                url: "/api/articles",
                params:{
                    limit:this.state.rows,
                    offset: this.state.totalPage * this.state.currentPage
                }
            }).then((item) => {
                // console.log(item,'1111111111111111');
                this.setState({
                    articles: item.data.articles,
                });
            });
        })
       
    }
    //点赞事件
    butClick = (item)	=>	{
        this.setState({
            favoritedisabled:true
        })
        //函数体
        console.log(item,'--------------');
        if(item.favorited){
             //取消点赞
            deleFavorite(item).then((ref)	=>	{
                //函数体
                console.log(ref,'取消点赞22222222222222');
            })
            item.favorited = false;
            item.favoritesCount += -1;
            // console.log(this.state.articles.indexOf(item));
            let key = this.state.articles.indexOf(item);
            this.state.articles[key] = item;
            this.setState({
                articles:this.state.articles
            }) 
        }else{
            //点赞
            addFavorite(item).then((ref)	=>	{
                //函数体
                console.log(ref,'点赞111111111111');
            })
            item.favorited = true;
            item.favoritesCount += 1;
            // console.log(this.state.articles.indexOf(item));
            let key = this.state.articles.indexOf(item);
            this.state.articles[key] = item;
            this.setState({
                articles:this.state.articles
            }) 
        }
        this.setState({
            favoritedisabled:false
        })
       
    }
    render() {
        return (
            <div>
                <div className="home-page">
                    <div className="banner">
                        <div className="container">
                            <h1 className="logo-font">conduit</h1>
                            <p>A place to share your knowledge.</p>
                        </div>
                    </div>

                    <div className="container page">
                        <div className="row">
                            <div className="col-md-9">
                                <div className="feed-toggle">
                                    <ul className="nav nav-pills outline-active">
                                        <li className="nav-item">
                                            <a className="nav-link disabled" href="/">
                                                Your Feed
                                            </a>
                                        </li>
                                        <li className="nav-item">
                                            <a className="nav-link active" href="/">
                                                Global Feed
                                            </a>
                                        </li>
                                    </ul>
                                </div>
                                {
                                this.state.articles.map((item, index) => {
                                    return <div key={index} className="article-preview">
                                        <div className="article-meta">
                                            <a href="profile.html">
                                                <img src={item.author.image} />
                                            </a>
                                            <div className="info">
                                                <a href="/" className="author">
                                                    {item.author.username}
                                                </a>
                                                <span className="date">{item.createdAt}</span>
                                            </div>
                                            <button onClick={this.butClick.bind(null,item)} disabled = {this.state.favoritedisabled} className="btn btn-outline-primary btn-sm pull-xs-right">
                                                <i className="ion-heart"></i> {item.favoritesCount}
                                            </button>
                                        </div>
                                        <a href="/" className="preview-link">
                                            <h1>{item.title}</h1>
                                            <p>{item.author.body}</p>
                                            <span>{item.description}</span>
                                        </a>
                                    </div>
                                    })
                                    // <Switch>
                                    //     <Route path="/home/global" component={Global}></Route>
                                    //     <Redirect to='/home/global'></Redirect>
                                    // </Switch>
                                }
                                <ul className="pagination">
                                    {
                                       Array.from({length: this.state.totalPage}).map((item,index)=>{
                                           return (
                                            <li className={this.state.currentPage === index ? "page-item ng-scope active" : "page-item ng-scope"} key={index}>
                                                <span  to='/home/global' className="page-link ng-binding"  style={{cursor:'pointer'}} onClick={this.click.bind(null,index)}>{index + 1}</span>
                                            </li>
                                           )
                                       }) 
                                    }
                                </ul>
                            </div>

                            <div className="col-md-3">
                                <div className="sidebar">
                                    <p>Popular Tags</p>

                                    <div className="tag-list">
                                        <a href="/" className="tag-pill tag-default">
                                            &zwnj;
                                        </a>
                                        <a href="/" className="tag-pill tag-default ">
                                            &zwnj;
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            &zwnj;{" "}
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            &zwnj;{" "}
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            &zwnj;{" "}
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            &zwnj;{" "}
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            &zwnj;{" "}
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            &zwnj;{" "}
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            &zwnj;{" "}
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            &zwnj;{" "}
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            HuManIty
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            Hu‌Man‌Ity
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            Gandhi
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            HITLER
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            SIDA
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            BlackLivesMatter
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            Black‌Lives‌Matter
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            test
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            dragons
                                        </a>
                                        <a href="/" className="tag-pill tag-default">
                                            butt
                                        </a>
                                    </div>
                                </div>
                            </div>



                        </div>
                    </div>
                </div>
            </div>
        );
    }
}
