import React, { Component } from 'react'
import { Link} from "react-router-dom";
import Cookie from 'js-cookie'
import pubsub from 'pubsub-js'
export default class Register extends Component {
  state = {
    userName:'',
    email:'',
    password:''
  }
  //获取input框的value值
  blur1 = (event)=>{
    this.setState({
      userName : event.target.value
    })
  }
  blur2 = (event)=>{
    this.setState({
      email : event.target.value
    })
  }
  blur3 = (event)=>{
    this.setState({
      password : event.target.value
    })
  }
//把获取到的value值提交
  submit = ()=>{
    let data = {
      username : this.state.userName,
      email : this.state.email,
      password : this.state.password
    }
    //通过/api/users进行注册
    window.api({
      method:'POST',
      url:'/api/users',
      data:{
        user:data
      }
    }).then((item)=>{
      //获取注册页面的token值
      Cookie.get('aoth',item.data.user.token)
      //发布
      pubsub.publish('ster',{datas:item.data})
      //重新指向
      this.props.history.push('/home')
    })
  }
  render() {
    return (
      <div>
        <div className="auth-page">
  <div className="container page">
    <div className="row">

      <div className="col-md-6 offset-md-3 col-xs-12">
        <h1 className="text-xs-center">Sign up</h1>
        <p className="text-xs-center">
          <a href="">Have an account?</a>
        </p>

        <ul className="error-messages">
          <li>That email is already taken</li>
        </ul>

        <form>
          <fieldset className="form-group">
            <input onBlur={(event) =>this.blur1(event)} className="form-control form-control-lg" type="text" placeholder="Your Name"/>
          </fieldset>
          <fieldset className="form-group">
            <input onBlur={(event)=>{this.blur2(event)}} className="form-control form-control-lg" type="text" placeholder="Email"/>
          </fieldset>
          <fieldset className="form-group">
            <input onBlur={(event)=>{this.blur3(event)}} className="form-control form-control-lg" type="password" placeholder="Password"/>
          </fieldset>
          <button className="btn btn-lg btn-primary pull-xs-right">
          <Link onClick={this.submit} to={Cookie.get("auth") ? '/home' : function(){return false}} style={{color:'#fff',textDecoration:'none'}}>Sign up</Link>
          </button>
        </form>
      </div>

    </div>
  </div>
</div>
      </div>
    )
  }
}
