import Cookie from 'js-cookie'
//点赞
export let addFavorite = (index) => {
  let slug = index.slug
  // console.log(index,'点赞----------')
  //定义一个拦截器
  window.api.interceptors.request.use( // 请求拦截
    function (config) {
      //携带token   保存到请求头
      config.headers.Authorization =
        "Token " + Cookie.get("auth");
      return config;
    },
    function(error){
      return Promise.reject(error)
    }
  )
  return window.api({
    method:'POST',
    url:`/api/articles/${slug}/favorite`
  })
}
//取消点赞
export let deleFavorite = (index) => {
  let slug = index.slug
  // console.log(index,'取消点赞=============')
  //定义一个拦截器
  window.api.interceptors.request.use( // 请求拦截
    function(config){
      //携带Token值，到请求头
      config.headers.Authorization = 
        "Token" + Cookie.get('auth');
      return config
    },
    function(error){
      return Promise.reject(error)
    }
  )
  return window.api({
    method:'DELETE',
    url:`/api/articles/${slug}/favorite`
  })
}