/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from "react";
import  Cookie from 'js-cookie'
import Pubsub from 'pubsub-js'
import { Link} from "react-router-dom";
export default class Login extends Component {
    state = {
        email:'',
        password:''
    }
    componentDidMount(){
        // window.api({
        //     method:'POST',
        //     url:'/api/users/login',
        //     data:{
        //       "user":{
        //         "email": "123123123@qq.com",
        //         "password": "123123123"
        //       }
        //     } 
        //   }).then((item)=>{
        //     console.log(item)
        //   })
    }
    submit = (event)=>{
        // event.preventDefault();
        // console.log('event',event);
        let data = {
            email:this.state.email,
            password:this.state.password
        }
        window.api({
            method:'POST',
            url:'/api/users/login',
            data:{
                user:data
            }
        }).then((item)=>{
            // console.log(item,'--------------');
            Cookie.set('auth',item.data.user.token)
            //发布订阅
            Pubsub.publish('auth',{datas:item.data})
            // Pubsub.publish('profile',{datas:item.data})
            this.props.history.push("/home")
        })
    }
    blur1 = (event)=>{
        // console.log(event.target.value);
        this.setState({
            email:event.target.value
        })
    }
    blur2 = (event)=>{
        // console.log(event.target.value);
        this.setState({
            password:event.target.value
        })
    }
    render() {
        // console.log(this.state,'----------------');
        return (
            <div>
                <div className="auth-page">
                    <div className="container page">
                        <div className="row">
                            <div className="col-md-6 offset-md-3 col-xs-12">
                                <h1 className="text-xs-center">Sign in</h1>
                                <p className="text-xs-center">
                                    <a href="">Have an account?</a>
                                </p>

                                <ul className="error-messages">
                                    <li>That email is already taken</li>
                                </ul>

                                <form onSubmit={this.submit}>
                                    <fieldset className="form-group">
                                        <input onBlur={(event) =>this.blur1(event)}
                                            className="form-control form-control-lg"
                                            type="text"
                                            placeholder="Email"
                                        />
                                    </fieldset>
                                    <fieldset className="form-group">
                                        <input onBlur={(event) =>this.blur2(event)}
                                            className="form-control form-control-lg"
                                            type="password"
                                            placeholder="Password"
                                        />
                                    </fieldset>
                                    <button  className="btn btn-lg btn-primary pull-xs-right"  >
                                        <Link to={Cookie.get("auth") ? "/home" : function(){return false}} onClick={this.submit} style={{color:'#fff',textDecoration:'none'}}>Sign in</Link>
                                    </button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}


